button.button.btn {
  height: pxToRem(55);
  background: rgb(42, 82, 153);
  background: linear-gradient(-90deg, rgba(42, 82, 153, 1) 0%, rgba(21, 41, 77, 1) 100%);
  border-radius: pxToRem(15);
  box-shadow: 0 pxToRem(3) pxToRem(6) 0 rgba(0, 0, 0, 0.16);
  border: 0;
  min-width: pxToRem(100);
  letter-spacing: pxToRem(0.5);
  color: $white;
  font-size: pxToRem(22);

  &.big {
    width: pxToRem(390);
    font-size: pxToRem(25);
    min-height: pxToRem(72);
  }

  &:disabled {
    background: $bg-disabled;
  }
}


@include breakpoint(sm992) {
  button.button.btn {
    height: pxToVW(25);
    border-radius: pxToVW(5);
    box-shadow: 0 pxToVW(3) pxToVW(6) 0 rgba(0, 0, 0, 0.16);
    min-width: pxToVW(80);
    letter-spacing: pxToVW(0.5);
    color: $white;
    font-size: pxToVW(12);
    padding: pxToVW(2) pxToVW(5);

    &.big {
      width: pxToVW(130);
      font-size: pxToVW(12);
      min-height: pxToVW(35);
      padding: pxToVW(5) pxToVW(10);
    }
  }
}